import { Component } from 'react';
import Issue from './Issue';
import Sidebar from './Sidebar';

import WrapperStyles from './../styles/WrapperStyles';

export default class Issues extends Component {
  state = {
    isLoaded: false,
    issues: [],
    username: '',
    token: ''
  };

  getIssues = () => {
    fetch(`https://api.github.com/search/issues?q=${this.state.username}`, {
      headers: {
        Authorization: `token ${this.state.token}`
      }
    })
      .then(response => response.json())
      .then(data => {
        console.log(data.items);
        this.setState({ isLoaded: true, issues: data.items });
      })
      .catch(error => {
        console.error(error);
        this.setState({ isLoaded: true, error });
      });
  };

  componentDidMount() {
    this.getIssues();
  }

  componentWillReceiveProps(prevProps) {
    if (prevProps === undefined) {
      return false;
    }
    this.setState({
      isLoaded: true
    });
    this.getIssues(prevProps);
  }

  // componentWillUnmount() {
  //   this.setState({
  //     username: '',
  //     token: '',
  //     issues: []
  //   });
  // }

  setForm = sidebarState => {
    const { token, username } = sidebarState;
    this.setState({ token, username, error: false });
    this.getIssues();
  };

  render() {
    const { error, isLoaded, issues } = this.state;

    return (
      <WrapperStyles>
        <Sidebar getForm={this.setForm} />
        {isLoaded ? <ul>{issues && issues.map((issue, i) => <Issue key={i} issue={issue} />)}</ul> : 'Loading...'}
      </WrapperStyles>
    );
  }
}
